import React from "react";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";

import {
	Basket,
	BorderStyle,
	BoundingBox,
	WindowDesktop,
} from "react-bootstrap-icons";

const ShopNav = ({ setDrawer }) => {
	const navigate = useNavigate();
	const inst = useSelector((state) => state.inst.activeInst);
	return (
		<>
			<p
				onClick={() => {
					setDrawer(false);
					window.scroll(0, 0);
					navigate(`shop-categories`);
				}}
			>
				<strong className="nav-link text-truncate">
					<BoundingBox /> Product Categories
				</strong>
			</p>
			<hr />
			<p
				onClick={() => {
					setDrawer(false);
					window.scroll(0, 0);
					navigate(`shop-products`);
				}}
			>
				<strong className="nav-link text-truncate">
					<Basket /> Shop Products
				</strong>
			</p>
			<hr />
			<p
				onClick={() => {
					setDrawer(false);
					window.scroll(0, 0);
					navigate(`shop-entries`);
				}}
			>
				<strong className="nav-link text-truncate">
					<BorderStyle /> Products Entries
				</strong>
			</p>
			<hr />
			<p
				onClick={() => {
					setDrawer(false);
					window.scroll(0, 0);
					navigate(`pos`);
				}}
			>
				<strong className="nav-link text-truncate">
					<WindowDesktop /> Point Of Sale
				</strong>
			</p>
			<hr />
		</>
	);
};

export default ShopNav;
