import React from "react";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { Dropdown } from "react-bootstrap";
import { PersonAdd, PersonGear } from "react-bootstrap-icons";
import MpesaNav from "./OnlinePay/Mpesa/MpesaNav";
const AdminNav = ({ setDrawer }) => {
	const navigate = useNavigate();
	const inst = useSelector((state) => state.inst.activeInst);
	return (
		<>
			<Dropdown style={{ width: "100%" }}>
				<Dropdown.Toggle variant="transparent" className="nav-link">
					<PersonGear /> Admin{" "}
				</Dropdown.Toggle>
				<Dropdown.Menu variant="dark">
					<Dropdown.Item>
						<p
							className="nav-link"
							onClick={() => {
								window.scroll(0, 0);
								setDrawer(false);
								navigate(`modes`);
							}}
						>
							Payment Modes
						</p>
					</Dropdown.Item>
					<MpesaNav setDrawer={setDrawer} navigate={navigate}></MpesaNav>
					<Dropdown.Item>
						<p
							className="nav-link"
							onClick={() => {
								window.scroll(0, 0);
								navigate(`edit`);
								setDrawer(false);
							}}
						>
							Edit Institution
						</p>
					</Dropdown.Item>
				</Dropdown.Menu>
			</Dropdown>
			<hr />

			<Dropdown style={{ width: "100%" }}>
				<Dropdown.Toggle variant="transparent" className="nav-link">
					<PersonAdd /> H.R. Manager
				</Dropdown.Toggle>
				<Dropdown.Menu variant="dark">
					<Dropdown.Item>
						<p
							className="nav-link"
							onClick={() => {
								window.scroll(0, 0);
								setDrawer(false);
								navigate(`staffs`);
							}}
						>
							Staffs
						</p>
					</Dropdown.Item>
					<Dropdown.Item>
						<p
							className="nav-link"
							onClick={() => {
								window.scroll(0, 0);
								setDrawer(false);
								navigate(`payroll-items`);
							}}
						>
							Payroll Items
						</p>
					</Dropdown.Item>{" "}
					<Dropdown.Item>
						<p
							className="nav-link"
							onClick={() => {
								window.scroll(0, 0);
								setDrawer(false);
								navigate(`payroll-summary`);
							}}
						>
							Payroll Summary
						</p>
					</Dropdown.Item>
				</Dropdown.Menu>
			</Dropdown>
			<hr />
		</>
	);
};

export default AdminNav;
